.customer-template-block #main.main-wrapper {
	max-width: 100%;
	background-color: #f8f8f8;
}

.account-details-wrapper {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	color: #666666;
	font-family: Montserrat;
	max-width: 1220px;
	margin: 0 auto;
	@include mq($global-breakpoint-md) {
		flex-wrap: nowrap;
	}
}

.account-sidebar {
	width: 100%;
	background-color: $color-white;
	border: 1px solid #EEEEEE;
	margin: 30px 15px 30px 10px;
	padding: 20px;
	@include mq($global-breakpoint-md) {
		width: 25%;
	}
}

.account-sidebar--list-item {
	background-repeat: no-repeat;
	background-position: left center;
	background-size: 15px auto;
	padding-left: 26px;
	&.link--my-groomsmen {
		background-size: 7px auto;
		background-position: 4px;
	}
}

.account-sidebar--list-item-link {
	font-size: 12px;
	line-height: 14px;
	font-weight: bold;
	text-transform: uppercase;
	padding: 8px 0;
	display: block;
}

.account-link {
	text-transform: uppercase;
	font-size: 12px;
	font-weight: bold;
	line-height: 16px;
	text-align: center;
	&.button--red {
		padding: 0.8rem 2rem;
		margin-left: 15px;
	}
}

.account-title {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-between;
	border-bottom: 1px solid #EEEEEE;
	padding-bottom: 20px;
	@include mq($global-breakpoint-md) {
		flex-wrap: nowrap;
	}
}

.account-title--header {
	font-family: "Playfair Display";
	font-size: 30px;
	font-weight: bold;
	line-height: 30px;
	margin-bottom: 15px;
	@include mq($global-breakpoint-md) {
		margin-bottom: 0;
	}
}

.account-main-content {
	width: 100%;
	background-color: $color-white;
	border: 1px solid #EEEEEE;
	margin: 0 10px 30px 15px;
	padding: 20px;
	@include mq($global-breakpoint-md) {
		width: 75%;
		margin: 30px 10px 30px 15px;
	}
}

.account-customer--name {
	font-size: 16px;
	line-height: 19px;
	text-transform: capitalize;
	padding: 20px 0;
	&:after {
		content: "";
		display: block;
		height: 2px;
		width: 35px;
		background-color: #EEEEEE;
		margin-top: 10px;
	}
}

.account-customer--single {
	border-bottom: 1px solid #EEEEEE;
	margin-bottom: 20px;
	padding-bottom: 4px;
}

.account-customer--label {
	font-size: 10px;
	line-height: 24px;
	text-transform: uppercase;
}

.account-customer--label-name {
	font-size: 13px;
	line-height: 16px;
	text-transform: capitalize;
}

.empty-addresses {
	text-align: center;
}

.empty-addresses--img {
	max-width: 300px;
	width: 100%;
}

.empty-addresses--content {
	padding-bottom: 20px;
}

.fake-page--our-guarntee {
	text-align: center;
	padding: 170px 50px;
}

.fake-page--our-guarntee {
	font-family: "Playfair Display";
	font-size: 28px;
	font-weight: bold;
	line-height: 30px;
	margin-bottom: 30px;
}

.fake-page--our-guarntee {
	font-size: 16px;
	line-height: 28px;
}

.order__title {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.title-left {
	display: flex;
	align-items: center;
	width: 50%;
	@include mq($global-breakpoint-md) {
		width: 84%;
	}
	.title-container {
		padding-right: 45px;
		&:last-child {
			padding-right: 0;
		}
		&:nth-child(2n),
		&:nth-child(3n) {
			display: none;
			@include mq($global-breakpoint-md) {
				display: block;
			}
		}
	}
}

.title-right {
	width: 50%;
	display: flex;
	align-items: center;
	@include mq($global-breakpoint-md) {
		width: 16%;
	}
	.icon--carret-down {
		color: #FE3A35;
		padding-left: 20px;
	}
	.order-dets {
		display: block;
		color: #FE3A35;	
		padding-top: 5px;
	}
}

.order-label {
	font-weight: bold;
	padding-bottom: 5px;
}

.address__info--wrapper {
	flex-wrap: wrap;
	margin-bottom: 20px;
	@include mq($global-breakpoint-md) {
		flex-wrap: nowrap;
		margin-bottom: 0;
	}	
}

.address__data {
	margin-bottom: 0;
	@include mq($global-breakpoint-md) {
		margin-bottom: 10px;
	}	
}

.address__title {
	margin-bottom: 10px;
	@include mq($global-breakpoint-md) {
		margin-bottom: 20px;
	}	
}

















































.footer-ctas {
	display: none;
	@include mq($global-breakpoint-md) {
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: #FE3A35;
		text-align: center;	
	}
}

.footer-cta--intro-title {
	font-family: "Playfair Display";
	font-size: 34px;
	font-weight: bold;
	line-height: 46px;
	text-align: center;
	color: $color-white;
}

.footer-cta--intro-content {
	color: $color-white;
	font-size: 13px;
}

.footer-ctas--contacts {
	text-align: center;
	color: $color-white;
	padding: 40px 20px;
}

.footer-ctas--contacts-title {
	font-size: 13px;
	font-weight: bold;
	line-height: 16px;
}

.footer-ctas--contacts-subtitle {
	font-size: 16px;
	font-weight: bold;
	line-height: 21px;
	padding: 5px 0;
}

.footer-ctas--contacts-content {
	font-size: 13px;
}




















